*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
body{

  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  
}
.Heading{
  text-align: center;
  margin: 50px;
}
.TaskItems{
  /* border: 1px solid rgba(255, 255, 255, 0.533); */
  box-shadow: 1px 1px 10px 2px rgba(218, 215, 9, 0.517);
  padding: 40px;
  width: 800px;
  height: 100%;
  border-radius: 10px;
  list-style-type: none;
  font-size: 20px;
}
.TaskItems li{
  margin: 20px;
  padding: 15px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Deletebtn{
  background-color: red;
  border: none;
  padding: 10px 20px;
  color: white;
  font-size: 17px;
  border-radius: 10px;

}
.completed{
  box-shadow: 0.1px 0.1px 10px 0.1px rgba(0, 255, 8, 0.493);
}
.notcompleted{
  box-shadow: 0.1px 0.1px 10px 0.2px rgba(255, 0, 0, 0.501);
}
/* .dnone{
  display: none;
} */
.toggle{
  background-color: blue;
  color: white;
  padding: 12px 24px;
  border: none;
  box-shadow: 1px 1px 10px 1px rgba(255, 255, 255, 0.371);
  font-size: 18px;
  border-radius: 10px;
  margin:0px 0px 10px 0% ;
}
.disnone{
  display: none;
}